

const Tile = ({tile}) => {
    return (
        <a className='tile' href={tile.url} target="_blank">
            <div className="tile-image">
                <img src={'https://www.khanacademy.org' + tile.thumb}/>
            </div>
            <div className="tile-details">
                <h3>{tile.title}</h3>
            </div>
            <div className="tile-footer">
                <span>{tile.sumVotesIncremented + " Votes Â· " + tile.spinoffCount + " Spin-offs"}</span>
            </div>
        </a>
    )
}

export default Tile
